<template>
  <div class="home_right_box">
    <li
      class="reveal-bottom paragraph"
    >十一月中旬开始，排名突然下降了，网站“个人博客”关键词排名从第一页第二名滑落到100页以后了，个人博客这个关键词百度已经搜不到了，仅有google、搜狗、360排名还在前几名。百度权重也变为0.之前群成员每天5个左右的增长速度，也渐渐变成5天一个。找了各种原因，删除了有问题的友链，也通知了其他博主暂时不换链接，一段时间下来，排名还是那样，没有变化。</li>
    <li
      class="reveal-bottom paragraph"
    >就这样熬到十二月中旬，干脆域名和空间都从之前公司转出来，备案转出期间网站打不开，趁着这个空档，就打算把网站重新改版。备案申请转出到申请成功花了12天的时间，新网站上线是在2号晚上完成的。也就是在3号百度有搜索关键词“个人博客网站”进来的。4号之后“个人博客”排名在前3页。百度权重1，PR3都已经恢复了。但是今天再看又不稳定了，不想再被百度折腾了，该怎么就怎么着吧...</li>
    <li
      class="reveal-bottom paragraph"
    >现在这个版本，内容页和列表页没有之前那么复杂了，介于logo的红心是桃红色，所以页面以绿色来作为亮点。首页也去掉了之前看起来有些臃肿的fatfooter，不是说之前的不好，只是现在想把个人博客做得简单点，就不想要太多的footer信息。</li>
  </div>
</template>

<script>
import scrollReveal from "scrollreveal";
export default {
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal(".reveal-bottom", {
      // 动画的时长
      duration: 1000,
      // 延迟时间
      delay: 500,
      // 动画开始的位置，'bottom', 'left', 'top', 'right'
      origin: "bottom",
      // 回滚的时候是否再次触发动画
      reset: false,
      // 在移动端是否使用动画
      mobile: false,
      // 滚动的距离，单位可以用%，rem等
      distance: "20px",
      // 其他可用的动画效果
      opacity: 0.001,
      easing: "linear",
      scale: 0.9,
    });
  },
};
</script>
<style lang="scss" scoped>
</style>


